:root{
    --background-height: 100vh;
    --background-width: 100vw;
}

*,*::before,*::after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* HAMBURGER BUTTON */

.hamburger-btn-container{
    display: flex;
    justify-content: flex-end;    
}

.hamburger-btn{
    display: none;
}

.hamburger-btn:hover{
    opacity: .3;
    cursor: pointer;
}

/* HAMBURGER CONTENT */

.hamburger-content-container{
    display: none;
}

/* TRANSPARENT BACKGROUND */

.background-image{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: var(--background-height);
    background-color: #000;
    overflow: hidden;
    transition: opacity .3s;
}   

.background-image::before{
    content: "";
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    /* background-image: url("../img/nature.jpg"); */
    background-image: url("../img/Home.jpeg");
    background-size: cover;
    background-position: top center;
    opacity: .6;
    transition: transform .2s;
}


.background-image h1,p{
    position: relative;
    color: #fff;
    font-size: 5rem;
    text-align: center;
    font-family: 'Oswald', sans-serif !important;
}

.background-image p{
    font-size: 2rem;
}

.background-image:hover{
    cursor: default;
}

.background-image::before{
    opacity: .3;
}

.background-image:hover::before{
    transform: scale(1.1);
    background-size: cover;
    background-position: top center;
}

/* Navbar section */

.navbar-container{
    background-color: transparent;
    z-index: 100;
}

.navbar-elements-container{
    list-style: none;
    text-align: center;
    font-family: 'Oswald', sans-serif !important;
}


.navbar-elements{
    padding: .8rem;
    color: #fff;
    font-weight: bolder;
    background-color: transparent;
    transition-duration: 300ms;
    border-radius: .5rem;
}

.navbar-elements:hover{
    background-color: #fff;
    border-radius: .5rem;
    cursor: pointer;
}

.navbar-elements:hover :first-child{
    color: #363131;
}

.navbar{
    position: absolute;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    top: 0;    
    left: 0;    
    right: 0;    
    bottom: 0;    
    margin-left: 2rem;
}



/* Social Media Icons */

.social-media-icons-container{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 2rem;
}

/* Each Font Awesome Media Icon */

.fab{
    color: #fff;
    text-decoration: none;
}

.fab:hover{
    opacity: .3;
    cursor: pointer;
}

/* Link to the website creator */

.designed-by-section{
    margin-top: 2rem;
    border: 2px solid #fff;
    padding: .4rem;
    background-color: transparent;
    transition: background-color 500ms;
    font-family: 'Oswald', sans-serif !important;
}

.designed-by-section:hover{
    cursor: pointer;
    background-color: #fff;
    border: 2px solid transparent;
    color: #000;
}

a{
    text-decoration: none;
    color: #fff;
}

.designed-by-section:hover a{
    color: #000;
}

.footer-container{
    display: none;
}

/* GALLERY SECTION */

.gray-background{
    background-color: #000;
    opacity: .9;
    height: 100vh;
}

.grid-container{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.grid-img-container{
    margin-top: 10rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 'auto';
    grid-gap: 1rem;
    grid-template-areas: 
        'grid-img-1 grid-img-2 grid-img-3'
        'grid-img-1 grid-img-5 grid-img-3'
        'grid-img-4 grid-img-5 grid-img-6'
        'grid-img-4 grid-img-7 grid-img-6';
}

.grid-img-1,
.grid-img-3,
.grid-img-4,
.grid-img-5,
.grid-img-6{
    height: 20rem;
    width: 15rem;
}

.grid-img-1{
    grid-area: grid-img-1;
}

.grid-img-2{
    grid-area: grid-img-2;
    height: 10rem;
    width: 15rem;
}

.grid-img-3{
    grid-area: grid-img-3;
}

.grid-img-4{
    grid-area: grid-img-4;
}

.grid-img-5{
    grid-area: grid-img-5;
}

.grid-img-6{
    grid-area: grid-img-6;
}

.grid-img-7{
    grid-area: grid-img-7;
    height: 15rem;
    width: 15rem;
}

/* CONTACT PAGE */

.contact{
    color: #fff;
    font-family: 'Oswald', sans-serif !important;
    text-align: center;
}

.contact > h1{
    padding: 1.5rem;
    font-size: 3rem;
}

.contact > p {font-size: 1.5rem;}

label{
    padding-left: 2rem;
}

.form-container{
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

tr{text-align: left;}

.submit-btn-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem;
}

.submit-btn{
    font-weight: bold;
    text-align: center;
    background-color: #555555;
    color: #fff;
    transition-duration: 0.4s;
    padding: 1rem;
    border-radius: .4rem;
    border: none;
    outline: none;
}

.submit-btn:hover{
    background-color: #fff;
    color: #000;
    cursor: pointer;
}

input{
    width: 25rem;
    height: 2rem;
    border: none;
    border-radius: 1rem;
    padding: 1rem;
}

input{
    outline: none !important;
    border: 3px solid #808080;
}
    
textarea{
    resize: none;
    min-width: 25rem;
    border: none;
    border-radius: 1rem;
    padding: 1rem;
}

textarea:focus{
    outline: none !important;
    border: 3px solid #808080;
}

/* MEDIA QUERIES */

@media screen and (max-width:950px) {

    .navbar-container{
        display: none;
    }

    .navbar-elements{
        color: #fff;
        font-weight: bolder;
        background-color: transparent;
        list-style: none;
    }

    .navbar-elements:hover{
        color: #fff;
        background-color: transparent;
        opacity: .3;
    }
    
    .hamburger-content-container{display: inline;}

    .hamburger-btn{
        display: block;
        position: absolute;
        z-index: 100;
        background-color: transparent;
        outline: none;
        border: none;
        font-size: 2rem;
        color: #fff;
        margin: 2rem;
        z-index: 2000;
    }

    .hamburger-elements-container{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        font-family: 'Oswald', sans-serif !important;
        color: #fff;
        position: absolute;
        height: 30%;
        width: 100%;
        background-color: #000;
        z-index: 1000;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: .9;
        font-size: 1.5rem;
        padding: 1em;
    }

    .footer-container{
        display: flex;
        justify-content: space-around;
        align-items: center;
        top: 100;
        left: 0;
        right: 0;
        bottom: 0;
        position: absolute;
        background-color: #000;
        width: 100%;
        height: 2rem;
    }

    .developer-name{
        text-decoration: none;
        color: #fff;
        font-family: 'Oswald', sans-serif !important;
    }

    .developer-name:hover{opacity: .3;}

    .icons{margin-left: 1rem;}

    .background-image h1,p{
        position: relative;
        text-align: center;
        color: #fff;
        font-family: 'Oswald', sans-serif !important;
        font-size: 4rem;
    }

}

@media screen and (max-width: 810px) {

    .gray-background{
        height: 105vh;
        overflow: hidden;
    }
    
    .grid-img-container{
        margin-left: 4rem;
        grid-template-areas: 
                    'grid-img-1 grid-img-2'
                    'grid-img-1 grid-img-5'
                    'grid-img-4 grid-img-5'
                    'grid-img-4 grid-img-7'
                    'grid-img-3 grid-img-7'
                    'grid-img-3 grid-img-6'
                    'grid-img-3 grid-img-6';
    }

    .grid-img-1,
    .grid-img-2,
    .grid-img-3,
    .grid-img-4,
    .grid-img-5,
    .grid-img-6,
    .grid-img-7{ width: 20rem;}

    .grid-img-3{height: 30rem;}
    
    .grid-img-7{height: 20rem;}
    
}

@media screen and (max-width:740px) {
    .grid-img-container{margin-right: 2.5rem;}
}
            

@media screen and (max-width:700px) {

    .grid-img-container{
        grid-template-areas: 
                    'grid-img-1 grid-img-2'
                    'grid-img-1 grid-img-2'
                    'grid-img-1 grid-img-2'
                    'grid-img-3 grid-img-4'
                    'grid-img-3 grid-img-4'
                    'grid-img-3 grid-img-4'
                    'grid-img-5 grid-img-6'
                    'grid-img-5 grid-img-6'
                    'grid-img-5 grid-img-6'
                    'grid-img-4 grid-img-7'
                    'grid-img-4 grid-img-7'
                    'grid-img-4 grid-img-7';
    }

}

@media screen and (max-width:650px) {
        .gray-background{height: 240vh;}
    
        .grid-img-container{
            grid-template-areas:
                    'grid-img-1 grid-img-1 grid-img-1'
                    'grid-img-2 grid-img-2 grid-img-2'
                    'grid-img-3 grid-img-3 grid-img-3'
                    'grid-img-4 grid-img-4 grid-img-4'
                    'grid-img-5 grid-img-5 grid-img-5'
                    'grid-img-6 grid-img-6 grid-img-6'
                    'grid-img-7 grid-img-7 grid-img-7';
        }

    .grid-img-1,
    .grid-img-2,
    .grid-img-3,
    .grid-img-4,
    .grid-img-5,
    .grid-img-6,
    .grid-img-7{
        height: 25rem;
        width: 25rem;
    }
            
}

@media screen and (max-width:450px) {
    .gray-background{
        height: 220vh;
    }
    
    .grid-img-container{
        margin-left: 1rem;
    }

    .grid-img-1,
    .grid-img-2,
    .grid-img-3,
    .grid-img-4,
    .grid-img-5,
    .grid-img-6,
    .grid-img-7{
        height: 15rem;
        width: 20rem;
    }
    
}